
function adjustFontSize(designWidth,fontBaseSize){
	var htmlWidth=document.documentElement.clientWidth;
	var htmlDom=document.getElementsByTagName('html')[0];
	var rate=fontBaseSize/designWidth
	htmlDom.style.fontSize=htmlWidth*rate+"px";
}
window.addEventListener("resize",function() {
	adjustFontSize(375,100)
})
adjustFontSize(375,100);



