<template>
	<div class="content">
	<header v-lazy-container="{ selector: 'img' }">
		<img data-src="../../assets/img/logo.png">
		<div id="home-search">
			<router-link to="/Search?will=Rent" tag="div" id="keyword">请输入游戏名称</router-link>
			<button id="home-search-btn"></button>
		</div>
	</header>
	<home-swiper :list="swiperList"></home-swiper>
	<!-- menu -->
	<icon-area :list="iconList" ></icon-area>
	<p class="hot">公告<a href="#">更多</a></p>
	<!-- 展示信息 -->
	<div class="msgBox">
		<ul>
			<li v-for="(item,index) of noticeList">
				<router-link :to="'/Article?id='+item.id" tag="a">
					<span>{{item.title}}</span>
					<span>{{item.time}}</span>
				</router-link>
			</li>
		</ul>
	</div>

	<!--  推荐商品-->
	<p class="hot">推荐商品<a href="#">更多</a></p>
	<div id="activeContent">
		<div class="imgBox">
			<router-link :to="'/Goods/detail?id='+item.id" tag="a" v-for="(item,index) of recommendList" :key="item.id">
				<span>
					<img v-lazy="item.imgUrl">
				</span>
				<p class="text">{{item.title}}</p>
			</router-link>
		</div>
	</div>
	<div class="callus">
		<a class="phone" href=""><i></i>电话联系</a>
		<a class="tencent" href="#"><i></i>QQ 4545454545</a>
	</div>
	<myfooter></myfooter>
	</div>


</template>
<script type="text/javascript">
	import homeSwiper from "./swiper.vue";
	import iconArea from "./iconArea.vue";
	import myfooter from "common/footer.vue";
	import axios from "axios"
	import qs from "qs"
	import {mapState} from "vuex";
	export default {
		components:{
			homeSwiper,
			iconArea,
			myfooter
		},
		data (){
			return{
				city:"",
				swiperList:[],
				iconList:[],
				noticeList:[],
				recommendList:[],
				lastSelect:''
			}
		},
		computed:{
			...mapState(['curSelect'])
		},
		name:"index",
		methods:{
			getHomeInfo (){
				axios.get("/api/index.json").then((res)=>{
					res=res.data;
					if(res.flag&&res.data){
						const data=res.data;
						this.city=data.city;
						this.swiperList=data.swiperList;
						this.iconList=data.iconList;
						this.noticeList=data.noticeList;
						this.recommendList=data.recommend;
						}						
					})
				// axios.post("/api/rent.php",qs.stringify({"name":"jiang","age":20})).then((res)=>{
				// 	console.log(res)
				// })
				// 
			}
			,
			// UploadFile:function(event){
			// 	    var formdata = new FormData();
			// 	    formdata.append('file',event.target.files[0]);
			// 	    formdata.append('action','test');
			// 	    axios.post('/api/test.php',formdata).then((res)=>{
			// 	    })

			// }
		},
		mounted (){
			this.lastSelect=this.curSelect;
			this.getHomeInfo();
		},
		updated (){
		},
		activated (){
			if(this.lastSelect!=this.curSelect){
				this.getHomeInfo()
				this.lastSelect=this.curSelect
			}
		}
	}
</script>
<style type="text/css" scoped> 
@import url("../../assets/css/index.css");

</style>


