import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home/index.vue'
// import Article from '@/pages/Article/article.vue'
// import Login from '@/pages/login.vue'
// import rentDetail from '@/pages/Rent/detail.vue'
import store from "../store";


function lazyLoading(name) {
  return function() {
    return import(`@/pages/${name}.vue`);
  }
}
// import City from '@/pages/City/city.vue'
// import Detail from '@/pages/Detail/detail.vue'
//第二种方式resolve => require(['@/pages/Detail/detail.vue'], resolve)
// const City = () => import('@/pages/City/city.vue')
Vue.use(Router)
const router=new Router({
  routes: [
  {
    path: '/',
    name: 'Home',
    meta:{
      title:'卡租购'
    },
    component: Home
  },
  {
    path: '/Search',
    name: 'Search',
    meta:{
      title:'搜索'
    },
    component: function() {
    	return import('@/pages/Search/index.vue')
    }
  },
  {
    path: '/Publish',
    name: 'Publish',
    meta:{
        requireAuth:true,
        title:'发布账号'
    },
    component: function() {
      return import('@/pages/Publish/index.vue')
    }
  },
  {
    path: '/Rent',
    name: 'Rent',
    meta:{
      title:"租号"
    },
    component: function() {
      return import('@/pages/Rent/index.vue')
    }
  },
  {
    path: '/User',
    name: 'User',
    meta:{
        title:'我的',
        requireAuth:true
    },
    component: function() {
      return import('@/pages/User/index.vue')
    }
  },
  {
    path:"/UserList",
    name:"UserList",
    meta:{
      title:"用户列表"
    },
    component:()=>import("@/pages/User/detail.vue")
  },
  {
    path: '/Login',
    name: 'Login',
    meta:{
      title:'登录'
    },
    component: lazyLoading('login')
  },
  {
    path: '/Article',
    name: 'Article',
    meta:{
      title:'文章详情'
    },
    component: lazyLoading('Article/article')
  },
  {
    path: '/Rent/detail',
    name: 'rentDetail',
    meta:{
      title:'租号详情'
    },
    component: lazyLoading('Rent/detail')
  },
  {
    path:"/Goods/detail",
    name: 'Goodsdetail',
    meta:{
      title:'商品详情'
    },
    component: resolve => require(['@/pages/Goods/detail.vue'], resolve)
  },
  {
    path: '/Goods',
    name: 'Goods',
    meta:{
      title:'商品'
    },
    component: resolve => require(['@/pages/Goods/index.vue'], resolve)
  }
  ]
  ,scrollBehavior (to,from,savedPosition){
      return { x: 0, y: 0 }
  }
})

router.beforeEach((to, from, next) => {
  if(to.meta.requireAuth) { // 如果路由项需要权限校验
    /* 
      从Vuex拿出token码，说明已登陆
     （前端的token可伪造，所以这并不是完全靠谱）
    */
    if (store.state.token==="KZG2605934892") { 
      next() // 正常跳转页面
    } else {
      next({
        path: '/Login',
        query: {redirect: to.fullPath}  
        /* 将跳转的路由地址作为参数带给登陆页，登录成功后跳转回该页面 */
      })
    }
  } else { // 如果不需要权限校验，直接进入路由页面
      next();
  }
})

router.afterEach(function (to) {
    if(to.meta.title){
       document.title = to.meta.title
    }
})

export default  router


