<!-- The ref attr used to find the swiper instance -->
<template>
  <swiper  :options="swiperOption" id="swiperBox" v-if="list.length">
    <swiper-slide class="swiper-slide" v-for="item of list" :key="item.id">
      <img :data-src="item.imgUrl" class="swiper-lazy">
    </swiper-slide>
    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper>
</template>

<script>
  export default {
    props:{
      list:Array
    },
    data() {
      return {
        swiperOption: {
          pagination:".swiper-pagination",
          autoplay:false,
          loop:true,
          lazyLoading:true,
          lazyLoadingInPrevNext:true
        }
      }
    }
  }
</script>
<style type="text/css" scoped>
#swiperBox>>>.swiper-pagination-bullet-active{
  background: #e6292c;
}
img{
  width: 100%;
  display: block;
}
 #swiperBox{
     height: 46vw;
     background: #eee;
}

</style>


