<template>
	<swiper  id="menuBox" :options="swiperOption">
	  <swiper-slide class="swiper-slide" v-for="(page,index) of pages" :key="index">
	  	<ul class="publicList">
		<router-link  tag="a"  v-for="(item,index) of page" 
		:to="item.hrefUrl" :key="index">
				<div class="img-wrap">
					<img v-lazy="item.imgUrl">
				</div>
				<p>{{item.title}}</p>
		</router-link>
		</ul>
	  </swiper-slide>
	</swiper>
</template>
<script type="text/javascript">

	export default{
		props:{
			list:Array
		},
		data() {
		  return {
		    swiperOption: {
		      autoplay:false
		    }
		  }
		},
		computed:{
			pages (){
				const pages=[]
				this.list.forEach(function(ele,index) {
					const page=Math.floor(index/4);
					if(!pages[page]){
						pages[page]=[]
					}
					pages[page].push(ele)
				})
				return pages;
			}
		}
	}
</script>
<style type="text/css" scoped>

#menuBox{
     padding: 0.08rem 0rem;
     margin-bottom: 0.01rem;
     background-color: #fff;
}

 .publicList{
 	display: flex;
}
 .publicList a{
     width: 25%;
     padding-bottom: 20%;
     position: relative;
     height: 0px;
}
 .publicList a:active{
     background: #f7f7f7;
}
.img-wrap{
	width: 50%;
	padding-bottom:50%;
	height:0px;
	margin: 0 auto 5px;
}
.img-wrap img{
	width: 100%;
	display: block;
}
.publicList a p{
     height: 0.16rem;
     text-align: center;
     line-height: 0.16rem;
     color: #333;
     font-size: 0.13rem;
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
}

</style>


