<template>
<footer>
	<ul id="footerList">
	    <li v-for="(item,index) of footerList" >
	        <router-link :to="item.path">
	            <svg><use :xlink:href="'static/img/symbol-defs.svg'+item.icon"></use></svg>
	            <span>{{item.text}}</span>
	        </router-link>
	    </li>
<!-- 	    
	    <li>
	        <router-link to="/" exact>
	            <svg><use xlink:href="static/img/symbol-defs.svg#icon-home-fill"></use></svg>
	            <span>首页</span>
	        </router-link>
	    </li>
	    <li>
	        <router-link to="/Goods">
	            <svg><use xlink:href="static/img/symbol-defs.svg#icon-category"></use></svg>
	            <span>商品</span>
	        </router-link>
	    </li>
	    <li>
	        <router-link to="/Rent">
	            <svg><use xlink:href="static/img/symbol-defs.svg#icon-rent-radius"></use></svg>
	            <span>租号</span>
	        </router-link>
	    </li>
	    <li>
	        <router-link to="/User">
	            <svg><use xlink:href="static/img/symbol-defs.svg#icon-user-fill"></use></svg>
	            <span>用户</span>
	        </router-link>
	    </li> -->
	</ul>
	</footer>
</template>
<script>
	import axios from "axios"
	export default{
		data (){
			return {
				footerList:[],
				selectedIndex:0
			}
		},
		mounted (){
			axios.get("/api/footer.json").then((res)=>{
				var res=res.data;
				if(res.flag&&res.data){
					var data=res.data;
					this.footerList=data
				}
			})
		}
	}
</script>
<style scoped>
	/* 底菜单栏 */
	 footer{
	     box-sizing: border-box;
	     -webkit-box-sizing: border-box;
	     position: fixed;
	     bottom: -0.01rem;
	     left: 0;
	     z-index: 9;
	     height: 0.48rem;
	     width: 100%;
	     padding: 0.03rem 0;
	     background-color: #fff;
	     box-shadow: 0 -0.02rem 0.04rem rgba(0,0,0,.1);
	}
	 footer ul{
	     display: flex;
	     height: 0.48rem;
	     overflow: hidden;
	}
	 footer li{
	     box-sizing: border-box;
	     -webkit-box-sizing: border-box;
	     -webkit-flex: 1;
	     height: 0.48rem;
	     font-size: 0.1rem;
	     color: #888;
	     text-align: center;
	     background: no-repeat center top;
	     background-size: 0.3rem;
	}
	 footer li:active{
	     background: #f7f7f7;
	}
	 footer li a{
	     font-size: inherit;
	     color: inherit;
	}
	 footer svg{
	     width: 0.28rem;
	     height: 0.28rem;
	     fill: #aaa;
	}
	 footer svg use{
	     display: block;
	}
	 footer .router-link-exact-active svg{
	     fill: #e6292c;
	     -webkit-animation: dou 0.3s;
	     animation: dou 0.3s;
	}
	 footer .router-link-exact-active span{
	     color: #e6292c;
	}
	
	footer li.selected svg{
		fill: #e6292c;
		-webkit-animation: dou 0.3s;
		animation: dou 0.3s;
	}
	 footer li.selected span{
	     color: #e6292c;
	}
	 @-webkit-keyframes dou{
	     0%{
	         transform: scale(0.5);
	    }
	     70%{
	         transform: scale(1.2);
	    }
	     100%{
	         transform: scale(1);
	    }
	}
	 @keyframes dou{
	     0%{
	         transform: scale(0.5);
	    }
	     70%{
	         transform: scale(1.2);
	    }
	     100%{
	         transform: scale(1);
	    }
	}
	 footer a{
	     display: block;
	     height: 100%;
	}
	 footer span{
	     display: block;
	     margin-top: -0.06rem;
	     color: #999;
	     font-size: 0.12rem;
	}
</style>


