import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);


let defaultSelect='全部游戏'
// try{
// 	if(localStorage.game){
// 		defaultSelect=localStorage.game
// 	}
// }catch(e){}

export default new Vuex.Store({
	state:{
		curSelect:defaultSelect,
		gameId:"all",
		token:sessionStorage.hasToken?sessionStorage.hasToken:false
	},
	actions:{
		gameChoose (ctx,playload){
			ctx.commit("myGameChange",playload)
		}
	},
	mutations:{
		myGameChange (state,payload){
			state.curSelect=payload.name;
			state.gameId=payload.id;
			// try{
			// 	localStorage.game=name;
			// }catch(e){
			// }
		},
		signIn(state,zhi){
			state.token=zhi;
			sessionStorage.hasToken=zhi;
		},
		signOut (state,zhi){
			state.token=zhi;
			sessionStorage.removeItem("hasToken");
		}
	},
	getters:{
		addUnit (state){
			return state.curSelect+" 加上去的哈哈哈哈"
		}
	}
})




